.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.backBtn {
  background-color: #000000;
  font-size: 1.1rem;
  display: flex;
  position: relative;
  margin-bottom: 6px;
  color: rgb(255, 255, 255);
  border-radius: 9px;
  cursor: pointer;
  transition: all 0.5s;
  padding: 8px;
}

.backBtn:hover {
  box-shadow: 5px 4px 11px rgba(132, 132, 32, 0.6);
  transform: scale(1, 1);
  background-color: white;
  color: black;
}

.backBtn:active {
  opacity: 0.4;
  background-color: black;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
